/**
 * Utility functions for handling token icons
 */

/**
 * Generates the Alcor UI GitHub icon URL for a token
 * @param symbol - Token symbol (e.g., 'WAX', 'WPIXAL')
 * @param contract - Token contract (e.g., 'eosio.token', 'pixeljourney')
 * @returns The Alcor UI GitHub icon URL
 */
export const getAlcorIconUrl = (symbol: string, contract: string): string => {
  const baseUrl = 'https://raw.githubusercontent.com/alcorexchange/alcor-ui/refs/heads/master/assets/tokens/wax'
  const fileName = `${symbol.toLowerCase()}_${contract.toLowerCase()}.png`
  return `${baseUrl}/${fileName}`
}

/**
 * Gets the appropriate token icon - tries Alcor UI first, falls back to emoji
 * @param symbol - Token symbol
 * @param contract - Token contract
 * @param onError - Optional callback when image fails to load
 * @returns Object with iconUrl and fallbackEmoji
 */
export const getTokenIcon = (symbol: string, contract: string, onError?: () => void) => {
  const iconUrl = getAlcorIconUrl(symbol, contract)
  const fallbackEmoji = getTokenEmoji(symbol)
  
  return {
    iconUrl,
    fallbackEmoji,
    onError
  }
}

/**
 * Gets emoji fallback for common tokens
 * @param symbol - Token symbol
 * @returns Emoji string
 */
export const getTokenEmoji = (symbol: string): string => {
  const emojiMap: Record<string, string> = {
    'WAX': '',
    'WPIXAL': '',
    'PXJ': '',
    'KING': '',
    'WAXUSDC': '',
    'WAXWBTC': '',
    'WUF': '',
    'LSWAX': '',
    'BRWL': '',
    'TLM': '',
    'WOMBAT': '',
    'TACO': '',
    'SHING': '',
    'YEET': '',
    'DMT': '',
    'FGL': '',
    'SOREN': ''
  }
  
  return emojiMap[symbol.toUpperCase()] || ''
}

/**
 * Creates an image element that tries to load the Alcor icon and falls back to emoji
 * @param symbol - Token symbol
 * @param contract - Token contract
 * @param className - CSS class for the image
 * @returns Promise that resolves to either an img element or emoji string
 */
export const createTokenIcon = (symbol: string, contract: string, className?: string): Promise<HTMLImageElement | string> => {
  return new Promise((resolve) => {
    const img = new Image()
    const iconUrl = getAlcorIconUrl(symbol, contract)
    const fallbackEmoji = getTokenEmoji(symbol)
    
    img.onload = () => {
      if (className) {
        img.className = className
      }
      img.alt = symbol
      resolve(img)
    }
    
    img.onerror = () => {
      resolve(fallbackEmoji)
    }
    
    img.src = iconUrl
  })
}

/**
 * Checks if an Alcor icon exists for a token
 * @param symbol - Token symbol
 * @param contract - Token contract
 * @returns Promise that resolves to boolean
 */
export const checkAlcorIconExists = async (symbol: string, contract: string): Promise<boolean> => {
  try {
    const iconUrl = getAlcorIconUrl(symbol, contract)
    const response = await fetch(iconUrl, { method: 'HEAD' })
    return response.ok
  } catch {
    return false
  }
}