import React, { createContext, useContext, useState, useEffect, useCallback, type ReactNode } from 'react'
import { useWallet } from './WalletContext'
import { apiService } from '../services/api'
import { log } from '../services/loggingService'
import type { LightApiBalance, LightApiResponse } from '../services/api'

interface TokenBalanceContextType {
  balances: Record<string, string>
  lightApiBalances: LightApiBalance[]
  isLoading: boolean
  refreshBalances: () => Promise<void>
  getTokenBalance: (symbol: string, contract?: string) => string
}

const TokenBalanceContext = createContext<TokenBalanceContextType | undefined>(undefined)

interface TokenBalanceProviderProps {
  children: ReactNode
}

export const TokenBalanceProvider: React.FC<TokenBalanceProviderProps> = ({ children }) => {
  const { account } = useWallet()
  const [balances, setBalances] = useState<Record<string, string>>({})
  const [lightApiBalances, setLightApiBalances] = useState<LightApiBalance[]>([])
  const [isLoading, setIsLoading] = useState(false)
  const [lastFetchTime, setLastFetchTime] = useState<number>(0)

  // Debounce API calls - only fetch if last fetch was more than 5 seconds ago
  const shouldFetch = useCallback(() => {
    const now = Date.now()
    return now - lastFetchTime > 5000 // 5 second debounce
  }, [lastFetchTime])

  const refreshBalances = useCallback(async () => {
    if (!account) {
      setBalances({})
      setLightApiBalances([])
      return
    }

    // Prevent spam - only fetch if enough time has passed
    if (!shouldFetch() && lightApiBalances.length > 0) {
      log.info('Skipping balance fetch - too recent', 'TokenBalanceContext')
      return
    }

    setIsLoading(true)
    try {
      log.info('Fetching token balances from Light API', 'TokenBalanceContext', { account })
      
      const lightApiResponse: LightApiResponse | null = await apiService.getLightApiBalances(account)
      
      if (lightApiResponse && lightApiResponse.balances) {
        const newBalances: Record<string, string> = {}
        
        // Store raw Light API balances
        setLightApiBalances(lightApiResponse.balances)
        
        // Process balances into formatted strings
        for (const balance of lightApiResponse.balances) {
          const key = `${balance.currency}-${balance.contract}`
          newBalances[balance.currency] = `${balance.amount} ${balance.currency}`
          newBalances[key] = `${balance.amount} ${balance.currency}`
        }
        
        setBalances(newBalances)
        setLastFetchTime(Date.now())
        
        log.info('Successfully loaded token balances', 'TokenBalanceContext', { 
          account, 
          tokenCount: lightApiResponse.balances.length 
        })
      } else {
        log.warn('No balances returned from Light API', 'TokenBalanceContext', { account })
        setBalances({})
        setLightApiBalances([])
      }
    } catch (error) {
      log.error('Failed to load token balances', 'TokenBalanceContext', error as Error, { account })
      setBalances({})
      setLightApiBalances([])
    } finally {
      setIsLoading(false)
    }
  }, [account, shouldFetch, lightApiBalances.length])

  // Get balance for a specific token
  const getTokenBalance = useCallback((symbol: string, contract?: string): string => {
    if (contract) {
      // Try with contract first for exact match
      const exactMatch = lightApiBalances.find(
        b => b.currency === symbol && b.contract === contract
      )
      if (exactMatch) {
        return `${exactMatch.amount} ${exactMatch.currency}`
      }
    }
    
    // Fallback to symbol only
    return balances[symbol] || `0.0000 ${symbol}`
  }, [balances, lightApiBalances])

  // Load balances when account changes
  useEffect(() => {
    refreshBalances()
  }, [account])

  // Set up periodic refresh every 30 seconds
  useEffect(() => {
    if (!account) return

    const interval = setInterval(() => {
      refreshBalances()
    }, 30000) // 30 seconds

    return () => clearInterval(interval)
  }, [account, refreshBalances])

  const value: TokenBalanceContextType = {
    balances,
    lightApiBalances,
    isLoading,
    refreshBalances,
    getTokenBalance
  }

  return (
    <TokenBalanceContext.Provider value={value}>
      {children}
    </TokenBalanceContext.Provider>
  )
}

export const useTokenBalance = (): TokenBalanceContextType => {
  const context = useContext(TokenBalanceContext)
  if (context === undefined) {
    throw new Error('useTokenBalance must be used within a TokenBalanceProvider')
  }
  return context
}