import React, { useState, useCallback, useEffect } from 'react'
import { useWallet } from '../contexts/WalletContext'
import { useTokenBalance } from '../contexts/TokenBalanceContext'
import { useNotifications } from '../hooks/useNotifications'
import TokenDropdown from './TokenDropdown'
import { log } from '../services/loggingService'
import { alcorService } from '../services/alcorService'
import { getAlcorIconUrl } from '../utils/tokenIconUtils'

import '../css/swap-interface.css'

interface SwapInterfaceProps {
  isModal?: boolean
  onClose?: () => void
  initialSellAmount?: number
  initialBuyAmount?: number
}

const SwapInterface: React.FC<SwapInterfaceProps> = ({ 
  isModal = false, 
  onClose, 
  initialSellAmount = 0,
  initialBuyAmount = 1
}) => {
  const { showSuccess, showError } = useNotifications()
  const { account, session } = useWallet()
  const { getTokenBalance } = useTokenBalance()
  const [sellAmount, setSellAmount] = useState<string>(initialSellAmount.toString())
  const [buyAmount, setBuyAmount] = useState<string>(initialBuyAmount.toString())
  const [sellToken, setSellToken] = useState<string>('WAX')
  const [buyToken, setBuyToken] = useState<string>('WPIXAL')
  const [loading, setLoading] = useState(false)
  const [quoteData, setQuoteData] = useState<any>(null)
  const [slippage, setSlippage] = useState<number>(0.5)
  const [showSlippageSettings, setShowSlippageSettings] = useState(false)
  const [alcorTokens, setAlcorTokens] = useState<any[]>([])
  const [isCalculating, setIsCalculating] = useState(false)
  const [lastInputField, setLastInputField] = useState<'sell' | 'buy'>('sell')

  // Load Alcor tokens
  const loadAlcorTokens = useCallback(async () => {
    try {
      const tokens = await alcorService.getAllTokens()
      setAlcorTokens(tokens)
      return tokens
    } catch (error) {
      log.error('Failed to load Alcor tokens', 'SwapInterface.loadAlcorTokens', error as Error)
      return []
    }
  }, [])

  // Favored tokens that should appear first and have priority
  const favoredTokenConfigs = {
    'PXJ': { 
      contract: 'pixeljourney', 
      precision: 8, 
      priority: 1,
      icon: getAlcorIconUrl('PXJ', 'pixeljourney')
    },
    'WPIXAL': { 
      contract: 'pixeljourney', 
      precision: 8, 
      priority: 2,
      icon: getAlcorIconUrl('WPIXAL', 'pixeljourney')
    },
    'WAX': { 
      contract: 'eosio.token', 
      precision: 8, 
      priority: 3,
      icon: getAlcorIconUrl('WAX', 'eosio.token')
    },
    'KING': { 
      contract: 'alpha.waxfun', 
      precision: 8, 
      priority: 4,
      icon: getAlcorIconUrl('KING', 'alpha.waxfun')
    },
    'WAXUSDC': { 
      contract: 'eth.token', 
      precision: 8, 
      priority: 5,
      icon: getAlcorIconUrl('WAXUSDC', 'eth.token')
    },
    'WAXWBTC': { 
      contract: 'eth.token', 
      precision: 8, 
      priority: 6,
      icon: getAlcorIconUrl('WAXWBTC', 'eth.token')
    },
    'WUF': { 
      contract: 'wuffi', 
      precision: 4, 
      priority: 7,
      icon: getAlcorIconUrl('WUF', 'wuffi')
    },
    'LSWAX': { 
      contract: 'token.fusion', 
      precision: 8, 
      priority: 8,
      icon: getAlcorIconUrl('LSWAX', 'token.fusion')
    },
    'BRWL': { 
      contract: 'brawlertoken', 
      precision: 4, 
      priority: 9,
      icon: getAlcorIconUrl('BRWL', 'brawlertoken')
    },
    'TLM': { 
      contract: 'alien.worlds', 
      precision: 4, 
      priority: 10,
      icon: getAlcorIconUrl('TLM', 'alien.worlds')
    },
    'WOMBAT': { 
      contract: 'wombattokens', 
      precision: 4, 
      priority: 11,
      icon: getAlcorIconUrl('WOMBAT', 'wombattokens')
    },
    'TACO': { 
      contract: 't.taco', 
      precision: 8, 
      priority: 12,
      icon: getAlcorIconUrl('TACO', 't.taco')
    },
    'SHING': { 
      contract: 't.taco', 
      precision: 8, 
      priority: 13,
      icon: getAlcorIconUrl('SHING', 't.taco')
    },
    'YEET': { 
      contract: 'token.yeet', 
      precision: 4, 
      priority: 14,
      icon: getAlcorIconUrl('YEET', 'token.yeet')
    },
    'DMT': { 
      contract: 'defiminingtk', 
      precision: 4, 
      priority: 15,
      icon: getAlcorIconUrl('DMT', 'defiminingtk')
    },
    'FGL': { 
      contract: 'fgltokenmgmt', 
      precision: 4, 
      priority: 16,
      icon: getAlcorIconUrl('FGL', 'fgltokenmgmt')
    },
    'SOREN': { 
      contract: 'farfromhomes', 
      precision: 4, 
      priority: 17,
      icon: getAlcorIconUrl('SOREN', 'farfromhomes')
    }
  }

  // Get token info from Alcor tokens or favored configs
  const getTokenInfo = useCallback((symbol: string) => {
    // First check favored tokens
    if (favoredTokenConfigs[symbol as keyof typeof favoredTokenConfigs]) {
      const config = favoredTokenConfigs[symbol as keyof typeof favoredTokenConfigs]
      return {
        symbol: symbol,
        contract: config.contract,
        precision: config.precision,
        token_id: `${symbol.toLowerCase()}-${config.contract.toLowerCase()}`,
        decimals: config.precision,
        icon: config.icon,
        logo: config.icon
      }
    }
    
    // Then check Alcor tokens
    const alcorToken = alcorTokens.find(token => token.symbol === symbol)
    if (alcorToken) {
      return {
        symbol: alcorToken.symbol,
        contract: alcorToken.contract,
        precision: alcorToken.decimals,
        token_id: `${alcorToken.symbol.toLowerCase()}-${alcorToken.contract.toLowerCase()}`,
        decimals: alcorToken.decimals,
        icon: alcorToken.logo || '/icons/default-token.png',
        logo: alcorToken.logo || '/icons/default-token.png'
      }
    }
    
    return null
  }, [alcorTokens])

  useEffect(() => {
    loadAlcorTokens()
  }, [])

  // Helper function to extract numeric balance from balance string
  const getNumericBalance = useCallback((symbol: string, contract?: string): number => {
    const balanceString = getTokenBalance(symbol, contract)
    const numericPart = balanceString.split(' ')[0]
    return parseFloat(numericPart) || 0
  }, [getTokenBalance])

  // Calculate swap amounts using Alcor API
  const calculateSwapAmount = useCallback(async (
    inputAmount: string,
    inputToken: string,
    outputToken: string,
    tradeType: 'EXACT_INPUT' | 'EXACT_OUTPUT'
  ) => {
    if (!inputAmount || parseFloat(inputAmount) <= 0 || !account) {
      return null
    }

    try {
      setIsCalculating(true)

      const inputTokenInfo = getTokenInfo(inputToken)
      const outputTokenInfo = getTokenInfo(outputToken)

      if (!inputTokenInfo || !outputTokenInfo) {
        log.error('Token info not found', 'SwapInterface.calculateSwapAmount')
        return null
      }

      let quote
      if (tradeType === 'EXACT_INPUT') {
        quote = await alcorService.calculateOutputAmount(
          inputTokenInfo,
          outputTokenInfo,
          inputAmount,
          account || 'market.pxj',
          slippage / 100
        )
      } else {
        quote = await alcorService.calculateInputAmount(
          inputTokenInfo,
          outputTokenInfo,
          inputAmount,
          account || 'market.pxj',
          slippage / 100
        )
      }

      setQuoteData(quote)
      return quote

    } catch (error) {
      log.error('Failed to calculate swap amount', 'SwapInterface.calculateSwapAmount', error as Error)
      return null
    } finally {
      setIsCalculating(false)
    }
  }, [account, slippage, getTokenInfo])

  // Handle sell amount change (EXACT_INPUT)
  const handleSellAmountChange = useCallback(async (value: string) => {
    setSellAmount(value)
    setLastInputField('sell')

    if (!value || parseFloat(value) <= 0) {
      setBuyAmount('')
      setQuoteData(null)
      return
    }

    const quote = await calculateSwapAmount(value, sellToken, buyToken, 'EXACT_INPUT')
    if (quote) {
      setBuyAmount(quote.output_amount)
    }
  }, [sellToken, buyToken, calculateSwapAmount])

  // Handle buy amount change (EXACT_OUTPUT)
  const handleBuyAmountChange = useCallback(async (value: string) => {
    setBuyAmount(value)
    setLastInputField('buy')

    if (!value || parseFloat(value) <= 0) {
      setSellAmount('')
      setQuoteData(null)
      return
    }

    const quote = await calculateSwapAmount(value, sellToken, buyToken, 'EXACT_OUTPUT')
    if (quote) {
      setSellAmount(quote.input_amount)
    }
  }, [sellToken, buyToken, calculateSwapAmount])

  // Recalculate when tokens change
  useEffect(() => {
    if (lastInputField === 'sell' && sellAmount) {
      handleSellAmountChange(sellAmount)
    } else if (lastInputField === 'buy' && buyAmount) {
      handleBuyAmountChange(buyAmount)
    }
  }, [sellToken, buyToken, lastInputField, sellAmount, buyAmount, handleSellAmountChange, handleBuyAmountChange])

  // Manual quote refresh function
  const getQuote = useCallback(async () => {
    if (lastInputField === 'sell' && sellAmount) {
      await handleSellAmountChange(sellAmount)
    } else if (lastInputField === 'buy' && buyAmount) {
      await handleBuyAmountChange(buyAmount)
    }
  }, [lastInputField, sellAmount, buyAmount, handleSellAmountChange, handleBuyAmountChange])

  const handleSwapTokens = () => {
    const tempToken = sellToken
    const tempAmount = sellAmount
    
    setSellToken(buyToken)
    setBuyToken(tempToken)
    setSellAmount(buyAmount)
    setBuyAmount(tempAmount)
  }

  const handleBalanceClick = () => {
    const balance = getNumericBalance(sellToken)
    handleSellAmountChange(balance.toString())
  }

  const handleSliderChange = (percentage: number) => {
    const balance = getNumericBalance(sellToken)
    const amount = (balance * percentage / 100).toFixed(4)
    handleSellAmountChange(amount)
  }

  const executeSwap = async () => {
    if (!account || !session) {
      showError('Please connect your wallet first')
      return
    }

    if (!quoteData) {
      showError('No quote available')
      return
    }

    try {
      setLoading(true)
      
      // Get token info for both sell and buy tokens
      const sellTokenInfo = getTokenInfo(sellToken)
      const buyTokenInfo = getTokenInfo(buyToken)
      
      if (!sellTokenInfo || !buyTokenInfo) {
        throw new Error(`Token configuration not found for ${!sellTokenInfo ? sellToken : buyToken}`)
      }
      
      // Use generic Alcor swap execution for any token pair
      const swapTransaction: any = await alcorService.executeSwapForPair(
        sellTokenInfo,
        buyTokenInfo,
        sellAmount,
        account,
        'EXACT_INPUT',
        slippage / 100
      )
      
      const result: any = await session.transact({ actions: swapTransaction.actions })
      
      showSuccess(`Successfully swapped ${sellAmount} ${sellToken} for ${buyAmount} ${buyToken}`)
      log.info('Swap transaction completed', 'SwapInterface.executeSwap', {
        sellAmount,
        sellToken,
        buyAmount,
        buyToken,
        transactionId: result?.transaction?.id || result?.transactionId || result?.id || 'unknown'
      })
      
      // Balances will be automatically refreshed by TokenBalanceContext
      
      // Reset form
      setSellAmount('0')
      setBuyAmount('0')
      setQuoteData(null)
      
      if (isModal && onClose) {
        onClose()
      }
    } catch (error) {
      log.error('Error executing swap', 'SwapInterface.executeSwap', error as Error)
      showError('Failed to execute swap transaction')
    } finally {
      setLoading(false)
    }
  }

  const content = (
    <div className={`swap-interface ${isModal ? 'modal-content' : ''}`}>
      {isModal && (
        <div className="swap-header">
          <h2>Swap Tokens</h2>
          <button className="close-button" onClick={onClose}></button>
        </div>
      )}
      
      <div className="swap-container">
        {/* Settings Button - moved to top right */}
        <div className="settings-container">
          <button 
            className="settings-toggle"
            onClick={() => setShowSlippageSettings(!showSlippageSettings)}
          >
            
          </button>
                    <button 
            className="refresh-quote-button" 
            onClick={getQuote}
            disabled={isCalculating || (!sellAmount && !buyAmount)}
            title="Refresh price quote"
          >
            {isCalculating ? '' : ''}
          </button>
        </div>

        {showSlippageSettings && (
          <div className="slippage-settings">
            <label>Slippage Tolerance</label>
            <div className="slippage-options">
              <button 
                className={slippage === 1.0 ? 'active' : ''}
                onClick={() => setSlippage(1.0)}
              >
                1%
              </button>
              <button 
                className={slippage === 5.0 ? 'active' : ''}
                onClick={() => setSlippage(5.0)}
              >
                5%
              </button>
              <button 
                className={slippage === 25.0 ? 'active' : ''}
                onClick={() => setSlippage(25.0)}
              >
                25.0%
              </button>
              <input
                type="number"
                value={slippage}
                onChange={(e) => setSlippage(parseFloat(e.target.value) || 0.5)}
                step="0.1"
                min="0.1"
                max="50"
                className="custom-slippage"
              />
            </div>
          </div>
        )}

        {/* Sell Section */}
        <div className="swap-section sell-section">
          <div className="swap-section-header">
            <span className="section-label">Sell</span>
            <span className="balance" onClick={handleBalanceClick} style={{ cursor: 'pointer' }}>
              Balance: {getNumericBalance(sellToken).toFixed(4)} {sellToken}
            </span>
          </div>
          
          <div className="input-container">
            <input
              type="number"
              value={sellAmount}
              onChange={(e) => handleSellAmountChange(e.target.value)}
              placeholder="0"
              className="amount-input"
            />
            
            <div className="token-selector">
              <TokenDropdown
                selectedToken={sellToken}
                onTokenSelect={setSellToken}
                filterByAvailable={false}
                className="sell-token-dropdown"
              />
            </div>
          </div>
          
          <div className="percentage-slider">
            <input
              type="range"
              min="0"
              max="100"
              value={sellAmount ? Math.round((parseFloat(sellAmount) / getNumericBalance(sellToken)) * 100) : 0}
              onChange={(e) => handleSliderChange(parseInt(e.target.value))}
              className="slider"
            />
            <div className="slider-labels">
              <span>0%</span>
              <span>25%</span>
              <span>50%</span>
              <span>75%</span>
              <span>100%</span>
            </div>
          </div>
          
        </div>

        {/* Swap Arrow */}
        <div className="swap-arrow-container">
          <button className="swap-arrow" onClick={handleSwapTokens}>
            
          </button>

        </div>

        {/* Buy Section */}
        <div className="swap-section buy-section">
          <div className="swap-section-header">
            <span className="section-label">Buy</span>
            <span className="balance">
              Balance: {getNumericBalance(buyToken).toFixed(4)} {buyToken}
            </span>
          </div>
          
          <div className="input-container">
            <input
              type="number"
              value={buyAmount}
              onChange={(e) => handleBuyAmountChange(e.target.value)}
              placeholder="0"
              className="amount-input"
              title="Enter desired output amount"
            />
            
            <div className="token-selector">
              <TokenDropdown
                selectedToken={buyToken}
                onTokenSelect={setBuyToken}
                filterByAvailable={false}
                className="buy-token-dropdown"
              />
            </div>
          </div>
        </div>

        {/* Quote Information */}
        {quoteData && (
          <div className="quote-info">
            <div className="quote-row">
              <span className="quote-label">Rate</span>
              <span className="quote-value">
                {quoteData.rate || '0.00340412'} WPIXAL per WAX (0.02%)
              </span>
            </div>
            <div className="quote-row">
              <span className="quote-label">Expected Output</span>
              <span className="quote-value">{quoteData.expectedOutput || buyAmount}</span>
            </div>
            <div className="quote-row">
              <span className="quote-label">Price Impact</span>
              <span className="quote-value">{quoteData.priceImpact || '< 0.01%'}</span>
            </div>
            <div className="quote-row">
              <span className="quote-label">Maximum Send after slippage</span>
              <span className="quote-value">
                {quoteData.maxSent || '309.45013892'} WAX (0.50%)
              </span>
            </div>
          </div>
        )}

        {/* Slippage Settings */}
        
        {/* Swap Button */}
        <button 
          className={`swap-button ${loading || !account || !quoteData || parseFloat(sellAmount) <= 0 ? 'disabled' : ''}`}
          onClick={executeSwap}
          disabled={loading || !account || !quoteData || parseFloat(sellAmount) <= 0}
        >
          {loading ? (
            <span className="loading-text">
              <span className="spinner"></span>
              Processing...
            </span>
          ) : !account ? (
            'Connect Wallet'
          ) : !quoteData ? (
            'Enter Amount'
          ) : parseFloat(sellAmount) <= 0 ? (
            'Enter Valid Amount'
          ) : (
            `Swap ${sellToken} To ${buyToken}`
          )}
        </button>
      </div>


    </div>
  )

  if (isModal) {
    return (
      <div className="swap-modal-overlay" onClick={onClose}>
        <div onClick={(e) => e.stopPropagation()}>
          {content}
        </div>
      </div>
    )
  }

  return content
}

export default SwapInterface